main {
    padding-left: 216px;
    padding-top: 47px;
}
.container-head h2 {
    font-family: opensans-light;
    color: #4e4c4c;
    font-weight: normal;
    font-size: 43px;
}
.container-head h2 span {
    color: #5885f5
}
.container-head p {
    font-family: opensans;
    font-size: 14px;
    line-height: 18px;
    color: #393838;
    margin-top: 21px;
    padding-right: 130px;
}
.filter-box {
    margin-top: 37px;
}
.filter-box .display-p {
    font-size: 14px;
    line-height: 24px;
    font-family: Roboto-regular;
    color: #8f8f93;
    display: inline-block;
    margin-right: 5px;
    margin-left: 3px;
}
.filter-box .export-select.select-item-content {
    width: 60px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
}
.export-select .option:nth-of-type(1) {
    margin-top: 0;
}

.filter-box .selected-item p {
    padding: 10px 15px 10px 8px;
    font-size: 14px;
    color: #a19c9c;
    font-family: Roboto-regular;
}
.filter-box .option-wrap p {
    color: #494951;
    padding: 10px 15px 10px 8px;
    font-family: Roboto-regular;
    font-size: 14px;
}
.filter-box .selected-item .option {
    border: none;
    background-color: #f6f7fc;
    background-image: url("../../../../assets/images/arrow-down.png");
}
.left-side-div {
    display: inline-block;
}
.filter-box .search-input {
    background-color: #f6f7fc;
    width: 234px;
    height: 38px;
    font-family: Roboto-regular;
    font-size: 14px;
    color: #222;
    text-indent: 28px;
    outline: none;
    border: none;
    background-image: url("../../../../assets/images/search-img2.png");
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-position: 8px center;
    float: right;
    margin-right: 14px;
}

.header-menu-btn {
    background-image: url(../../../../assets/images/menu-img-white.png);
    background-repeat: no-repeat;
    width: 26px;
    height: 17px;
    position: absolute;
    top: 8px;
    left: 10px;
    cursor: pointer;
    display: none;
}
.container-head .add-number-btn {
    border: none;
    outline: none;
    background-color: #5885f5;
    color: #fff;
    font-size: 16px;
    font-family: opensans;
    border-radius: 4px;
    width: 185px;
    height: 47px;
    margin-top: 10px;
    margin-left: 3px;
    margin-bottom: 13px;
}


.check-portability-content {
    width: 63%;
    margin-top: 39px;
    margin-left: 3px;
    margin-bottom: 155px;
    padding-left: 31px;
}
.check-portability-content label {
    font-family: Roboto-bold;
    color: #4a4a52;
    font-size: 11px;
    line-height: 24px;
    display: block;
}
.type-select {
    width: 68%;
    display: inline-block;
}
.type-select .option:nth-of-type(1) {
    margin-top: 0;
}
.type-select .option {
    height: 38px;
}
.type-select .first-option {
    height: 47px;
    border-radius: 4px;
}
.type-select p {
    padding: 12px 10px;
    color: #9e9b9a;
    font-family: opensans;
    font-size: 16px;
}

.checkbox-content {
    margin-top: 50px;
}
.checkbox-content p {
    font-family: Roboto-regular;
    font-size: 14px;
    color: #4a4a52;
    line-height: 24px;
    display: inline-block;
    margin-left: 4px;
    width: calc(100% - 30px);
    vertical-align: top;
}
.enter-number-content {
    background-color: #f6f7fc;
    padding: 10px 23px 23px 23px;
    margin-top: 14px;
}
.enter-number-content label {
    font-family: Roboto-bold;
    font-size: 11px;
    line-height: 24px;
    color: #4a4a52;
    display: block;
    text-transform: uppercase;
}
.enter-number-content .add-more-a {
    font-family: opensans-semibold;
    color: #5885f5;
    font-size: 15px;
    line-height: 29px;
    margin-left: 10px;
    cursor: pointer;
}
.enter-number-content input {
    background-color: #fff;
    height: 47px;
    outline: none;
    border: 1px solid #d8dbdc;
    border-radius: 4px;
    font-family: Roboto-regular;
    font-size: 16px;
    color: #323231;
    width: 73%;
    text-indent: 10px;
}
.check-portability-btn {
    background-color: #5885f5;
    font-family: opensans;
    color: #fff;
    font-size: 16px;
    border: none;
    outline: none;
    border-radius: 4px;
    width: 186px;
    height: 47px;
    float: right;
    margin-top: 19px;
}
.main-content hr {
    background-color: #000;
    border-color: #000;
    height: 5px;
}
.porting-process-content {
    background-color: #5885f5;
    padding: 28px 26px 88px 26px;
}
.porting-process-content h2 {
    font-family: opensans-light;
    color: #fff;
    font-size: 43px;
    font-weight: normal;
}
.porting-process-content p {
    font-family: opensans;
    color: #fff;
    font-size: 14px;
    margin-top: 15px;
    margin-left: 4px;
}
.porting-process-content a {
    font-family: opensans;
    color: #fff;
    font-size: 14px;
    text-decoration: underline;
    margin-left: 4px;
    margin-top: 17px;
    display: block;
    margin-bottom: 43px;
}
.porting-process-content span {
    font-family: opensans-bold;
    color: #fff;
    font-size: 14px;
    margin-top: 30px;
    display: block;
    margin-left: 4px;
}
.container-head {
    padding-left: 31px;
}


@media (max-width: 1050px) {
    .check-portability-content {
        width: calc(100% - 3px);
        padding-right: 31px;
    }
}
@media (max-width: 1000px) {
    .container-head p {
        padding-right: 10px;
    }
}
@media (max-width: 768px) {
    header {
        height: 30px;
    }
    main {
        padding-left: 0px;
    }
    aside {
        display: none;
    }
    header .col-10 {
        width: 94%;
    }
    .header-menu-btn {
        display: inline-block;
    }
}
@media (max-width: 950px) {
    .filter-box .search-input {
        float: initial;
        display: block;
        margin-top: 10px;
    }
    .csv-content {
        float: initial;
        margin-left: 15px;
        display: inline-block;
    }
}

@media (max-width: 600px) {
    .filter-box .search-input {
        width: 100%;
    }
    .container-head h2 {
        font-size: 25px;
    }
    .left-side-div {
        width: 100%;
    }
    .filter-box .action-select.select-item-content {
        width: 100%;
        margin-top: 20px;
        display: block;
    }
    .type-select {
        width: 100%;
    }
    .enter-number-content input {
        width: 100%;
    }
}



