
aside {
  background-color: #323231;
  height: 100%;
  width: 216px;
  padding: 26px 10px 0 10px;
  display: inline-block;
  position: fixed;
  z-index: 8000;
}
.user-info {
  margin-top: 38px;
  margin-left: 12px;
}
.user-info p {
  color: #fff;
  font-family: opensans;
  font-size: 16px;
  line-height: 36px;
}
.user-info .logout {
  color: #818181;
  font-size: 12px;
  line-height: 24px;
  font-family: opensans;
}
.user-info .log-out-btn {
  width: 16px;
  height: 16px;
  background-image: url("./assets/images/logout.png");
  background-size: cover;
  background-repeat: no-repeat;
  border: none;
  outline: none;
  background-color: #323231;
  vertical-align: middle;
  margin-left: 1px;
}
.aside-content ul {
  list-style: none;
  margin-top: 55px;
  margin-left: 5px;
  height: 70%;
  overflow-y: auto;
  overflow-x: hidden;
}
.aside-content ul::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(230, 230, 230, 0.3);
  background-color: #7f7f7f;
}
.aside-content ul::-webkit-scrollbar {
  width: 8px;
  background-color: #7f7f7f;
}
.aside-content ul::-webkit-scrollbar-thumb {
  background-color: #6e6e6e;
  border: 2px solid #7f7f7f;
}
.aside-content ul li {
  margin-bottom: 16px;
}
li .li-content .dashboard-item {
  background-image: url("./assets/images/dashboard.png");
  background-size: 17px 17px;
  background-repeat: no-repeat;
  text-indent: 21px;
  color: #818181;
  font-family: Roboto-bold;
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  background-position: 0 center;
}
li .li-content .billing-item {
  background-image: url("./assets/images/billing.png");
  background-size: 17px 19px;
  background-repeat: no-repeat;
  text-indent: 21px;
  color: #818181;
  font-family: Roboto-bold;
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  background-position: 0 center;
  display: inline-block;
}
.li-content .billing-arrow.arrow-content {
  margin-top: -2px;
}
li .li-content .origination-item {
  background-image: url("./assets/images/origination.png");
  background-size: 18px 18px;
  background-repeat: no-repeat;
  text-indent: 21px;
  color: #ffffff;
  font-family: opensans;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  background-position: 0 center;
  margin-top: 10px;
  display: inline-block;
}
.li-content span {
  display: block;
  color: #ffffff;
  font-family: opensans;
  font-size: 13px;
  line-height: 24px;
  margin-left: 36px;
  cursor: pointer;
}
.li-content span a {
  color: #ffffff;
  text-decoration: none;
}
.li-content span.active-item {
  color: #5885f5;
}
.li-content .arrow-content {
  float: right;
  margin-top: 13px;
  margin-right: 5px;
  cursor: pointer;
}
.li-content .arrow-content i {
  color: #bfbfbe;
}
